import React from 'react';

const SimpleLogo = ({ size = 64, className = '' }) => {
  return (
    <div className={`flex items-center justify-center ${className}`}>
      <svg 
        width={size} 
        height={size} 
        viewBox="0 0 32 32" 
        xmlns="http://www.w3.org/2000/svg"
      >
        <defs>
          <linearGradient id="simple-quill" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style={{ stopColor: '#5B3C88', stopOpacity: 1 }} />
            <stop offset="100%" style={{ stopColor: '#8B5CF6', stopOpacity: 1 }} />
          </linearGradient>
          
          <linearGradient id="simple-book" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" style={{ stopColor: '#F59E0B', stopOpacity: 1 }} />
            <stop offset="100%" style={{ stopColor: '#F97316', stopOpacity: 1 }} />
          </linearGradient>
        </defs>
        
        {/* Background circle */}
        <circle cx="16" cy="16" r="15" fill="url(#simple-quill)" opacity="0.1"/>
        
        {/* Book */}
        <rect x="13" y="8" width="6" height="16" fill="url(#simple-book)" rx="1"/>
        <rect x="14" y="9" width="4" height="14" fill="white" rx="0.5"/>
        
        {/* Book lines */}
        <g stroke="#E5E7EB" strokeWidth="0.5" opacity="0.6">
          <line x1="15" y1="11" x2="17" y2="11"/>
          <line x1="15" y1="12" x2="17" y2="12"/>
          <line x1="15" y1="13" x2="17" y2="13"/>
          <line x1="15" y1="14" x2="17" y2="14"/>
          <line x1="15" y1="15" x2="17" y2="15"/>
          <line x1="15" y1="16" x2="17" y2="16"/>
          <line x1="15" y1="17" x2="17" y2="17"/>
          <line x1="15" y1="18" x2="17" y2="18"/>
          <line x1="15" y1="19" x2="17" y2="19"/>
          <line x1="15" y1="20" x2="17" y2="20"/>
        </g>
        
        {/* Quill Pen */}
        <path d="M 19 12 Q 22 9 25 11 Q 23 14 20 16 L 19 12" fill="url(#simple-quill)"/>
        <path d="M 19 12 Q 17 11 16 12 Q 17 13 18 14 L 19 12" fill="#E5E7EB" opacity="0.8"/>
        <path d="M 20 16 L 21 17 L 20 18 L 19 17 Z" fill="#1F2937"/>
        
        {/* Ink drop */}
        <circle cx="20" cy="19" r="1" fill="#1F2937" opacity="0.8"/>
      </svg>
    </div>
  );
};

export default SimpleLogo; 